* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: black url("https://source.unsplash.com/random/1600x900?white") no-repeat fixed center;
  width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
}

.wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.wrapper #box {
  width: 500px;
  height: 500px;
  position: relative;
  overflow: hidden;
  margin: auto;
  border: 1px solid black;
}

.wrapper button {
  border: none;
  background: black;
  padding: 10px 20px;
  border-radius: 30px;
  color: white;
  cursor: pointer;
  font-weight: bold;
  margin: auto;
  transform: translateY(-30px);
}

.confetti:nth-child(0) {
  position: absolute;
  width: 3px;
  height: 1.2px;
  background-color: orange;
  top: -10%;
  animation: drop-0 5.5452875365s 0.735225505s 1, spin-1 0.3s ease infinite;
}

.tri:nth-child(50) {
  position: absolute;
  top: -10%;
  border-bottom: 4.5px solid green;
  border-right: 1.5px solid transparent;
  border-left: 1.5px solid transparent;
  animation: drop-0 5.0630395414s 0.5709725348s 1, spin-1 0.3s ease infinite;
}

.ribbon:nth-child(100) {
